.sendForm {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;

}

.textarea {
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    height: 40px;
    padding: 10px 24px;
    font-size: 16px;
    border-radius: 20px;
    background: #F5F7FB;
    box-shadow: 0px 1px 2px 0px rgba(29, 33, 38, 0.10), 0px 5px 20px 0px rgba(29, 33, 38, 0.03);
    resize: none;
    overflow: hidden;
    width: 100%;
    margin-right: 40px;
}

.button {
    border-radius: 20px;
    border: 0;
    background: #06C;
    color: #FFF;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding: 10px 40px;
    height: 40px;
}

.button:hover {
    background: rgb(0, 65, 129);
}

.button:active {
    background: rgb(0, 65, 129);
}

textarea::-webkit-scrollbar {
    width: 0;
    height: 0;
}