.sidebar {
    display: none;
}

.sidebar.open {
    z-index: 1;
    padding: 16px 24px 0 70px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    gap: 23px;
    height: 100vh;
    width: 317px;
    background: #FFF;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.10), 0 10px 40px 0 rgba(29, 33, 38, 0.13);
    position: absolute;
}

.background {
    left: 317px;
    z-index: 1;
    width: calc(100% - 317px);
    height: 100vh;
    opacity: 0.3;
    background: #000;
    position: absolute;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px
}

.nav a {
    color: #000;
    font-family: Montserrat, sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: unset;
    text-decoration: none;
}


.nav a:hover {
    color: #58ACFF;
    font-family: Montserrat, sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.nav a.active {
    color: #0059B2;
    font-family: Montserrat, sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-decoration-line: underline;
}

.close {
    display: flex;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    align-self: end;
}

.close img {

}

.active {
    color: red;
}